@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
* {
  margin: 0;
  padding: 0;
  list-style: none; }

/* RWD img css*/
img {
  max-width: 100%;
  height: auto; }

/* 全域 border box*/
*, *::before, *::after {
  box-sizing: border-box; }

h2 {
  font-size: 48px;
  line-height: 72px;
  text-align: center;
  padding-top: 60px;
  padding-bottom: 40px;
  color: #650300; }

.container {
  width: 1920px; }

.wrap {
  width: 1110px;
  margin: 0 auto; }
  @media (max-width: 992px) {
    .wrap {
      width: 690px; } }
  @media (max-width: 767px) {
    .wrap {
      width: 345px; } }

.header {
  width: 100%;
  background-color: #AA0601;
  display: flex;
  justify-content: space-between;
  padding: 0 88px; }
  @media (max-width: 992px) {
    .header {
      padding: 0 39px; } }
  @media (max-width: 767px) {
    .header {
      padding: 0;
      flex-direction: column; } }
  .header .logo {
    width: 80px;
    margin-top: 30px; }
    @media (max-width: 767px) {
      .header .logo {
        margin: 10px auto 10.86px auto;
        display: flex;
        justify-content: center; } }
    .header .logo img {
      display: block; }
      @media (max-width: 767px) {
        .header .logo img {
          width: 48px;
          height: 29.14px; } }
  @media (max-width: 767px) {
    .header .navbar {
      width: 100%; } }
  .header .navbar ul {
    display: flex;
    flex-wrap: wrap; }
    .header .navbar ul li {
      margin-right: 64px;
      margin-top: 38px;
      margin-bottom: 41px; }
      .header .navbar ul li a {
        font-size: 20px;
        color: #fff;
        text-decoration: none; }
      @media (max-width: 767px) {
        .header .navbar ul li {
          width: 50%;
          margin: 0;
          padding-top: 8px;
          padding-bottom: 8px;
          display: flex;
          justify-content: center;
          align-items: center;
          border: 1px solid #fff; } }
    .header .navbar ul li:last-child {
      margin-right: 0; }

.banner img {
  width: 100%;
  position: relative;
  display: block; }

.banner p {
  color: #650300; }

.banner .banner-text1 {
  font-size: 36px;
  position: absolute;
  top: 348px;
  left: 1013px; }

.banner .banner-text2 {
  position: absolute;
  font-size: 64px;
  font-variant: bold;
  top: 396px;
  left: 939px; }

.ad {
  width: 100%;
  height: 481px;
  background-color: #AA06010D; }
  .ad ul {
    display: flex;
    justify-content: space-between; }
    .ad ul li {
      width: 255px;
      display: flex;
      flex-direction: column;
      align-items: center; }
      .ad ul li .img {
        width: 100px;
        height: 100px;
        display: flex;
        justify-content: center;
        align-items: center; }
      .ad ul li h3 {
        font-size: 24px;
        line-height: 36px;
        margin-top: 16px;
        margin-bottom: 8px;
        color: #650300; }

.products ul > li {
  width: 100%; }
  .products ul > li img {
    width: 635px; }

.footer {
  font-family: 'Roboto', sans-serif;
  background-color: #AA0601; }
  .footer .content {
    padding-top: 32px;
    padding-bottom: 24px;
    border-bottom: 1px solid #fff; }
    @media (max-width: 992px) {
      .footer .content {
        padding-bottom: 20px; } }
    @media (max-width: 767px) {
      .footer .content {
        display: flex;
        justify-content: space-between;
        flex-direction: row-reverse;
        align-items: flex-start;
        padding-top: 25px;
        padding-bottom: 24px; } }
    .footer .content .ul-list {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 24px; }
      .footer .content .ul-list ul {
        display: flex; }
      @media (max-width: 767px) {
        .footer .content .ul-list .footer-a {
          display: none; } }
      .footer .content .ul-list .footer-a li {
        margin-right: 48px; }
        @media (max-width: 992px) {
          .footer .content .ul-list .footer-a li {
            margin-right: 40px; } }
        .footer .content .ul-list .footer-a li a {
          text-decoration: none;
          color: #fff;
          font-size: 16px;
          line-height: 24px; }
        .footer .content .ul-list .footer-a li:last-child {
          margin-right: 0; }
      .footer .content .ul-list .footer-icon {
        justify-content: space-between; }
        .footer .content .ul-list .footer-icon li {
          width: 48px;
          height: 48px;
          margin-right: 32px; }
          @media (max-width: 992px) {
            .footer .content .ul-list .footer-icon li {
              width: 24px;
              height: 24px;
              margin-right: 24px; } }
          .footer .content .ul-list .footer-icon li:last-child {
            margin-right: 0; }
    .footer .content .detail li {
      display: flex;
      align-items: center;
      margin-bottom: 16px; }
      @media (max-width: 992px) {
        .footer .content .detail li {
          margin-bottom: 12px; } }
      @media (max-width: 767px) {
        .footer .content .detail li {
          margin-bottom: 6px; } }
      .footer .content .detail li:last-child {
        margin-bottom: 0px; }
      .footer .content .detail li .img {
        width: 48px;
        height: 48px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 16px; }
        @media (max-width: 992px) {
          .footer .content .detail li .img {
            width: 24px;
            height: 24px;
            margin-right: 8px; } }
        @media (max-width: 767px) {
          .footer .content .detail li .img {
            width: 20px;
            height: 20px;
            margin-right: 5px; } }
        .footer .content .detail li .img img:nth-child(1) {
          width: 36px;
          height: 36px; }
          @media (max-width: 992px) {
            .footer .content .detail li .img img:nth-child(1) {
              width: 18px;
              height: 18px; } }
          @media (max-width: 767px) {
            .footer .content .detail li .img img:nth-child(1) {
              width: 15px;
              height: 15px; } }
        .footer .content .detail li .img img:nth-child(2) {
          width: 40px;
          height: 32px; }
          @media (max-width: 992px) {
            .footer .content .detail li .img img:nth-child(2) {
              width: 20px;
              height: 16px; } }
          @media (max-width: 767px) {
            .footer .content .detail li .img img:nth-child(2) {
              width: 16.67px;
              height: 13.33px; } }
      .footer .content .detail li p {
        font-size: 36px;
        line-height: 54px;
        color: #fff; }
        @media (max-width: 992px) {
          .footer .content .detail li p {
            font-size: 20px;
            line-height: 30px; } }
        @media (max-width: 767px) {
          .footer .content .detail li p {
            font-size: 16px; } }
  .footer .copy-right {
    padding-top: 32px;
    padding-bottom: 39px;
    display: flex;
    justify-content: space-between;
    align-items: center; }
    @media (max-width: 992px) {
      .footer .copy-right {
        padding-top: 24px;
        padding-bottom: 32px; } }
    @media (max-width: 767px) {
      .footer .copy-right {
        padding-top: 16px;
        padding-bottom: 24px;
        flex-direction: column;
        align-items: flex-start; } }
    .footer .copy-right .text {
      color: #fff;
      font-size: 16px;
      line-height: 24px; }
      @media (max-width: 767px) {
        .footer .copy-right .text {
          margin-bottom: 8px; } }
    .footer .copy-right ul {
      display: flex; }
      @media (max-width: 767px) {
        .footer .copy-right ul {
          flex-direction: column; } }
      .footer .copy-right ul li {
        margin-right: 48px; }
        .footer .copy-right ul li a {
          color: #fff;
          text-decoration: none; }
        @media (max-width: 767px) {
          .footer .copy-right ul li {
            margin-bottom: 8px; } }
        .footer .copy-right ul li:last-child {
          margin: 0; }
